	module.exports = {"data":{"post":{"html":"<p>Wouldn't you know. As soon as the weather gets warm (above freezing) and Mama's off on her first trip of the year, I get the flu. Nothing serious, the usual headache and sore throat that ibuprofin and pinot grigio can handle in the short run, but enough to come home early. My co-workers don't like working with folks who are infectious. Of course, Christopher doesn't issue sick days, but there's always the <a href=\"http://www.amazon.de/exec/obidos/ASIN/B00005UQZ6\">Kleine Eisbär</a> to keep him occupied.</p>\n<p>No, Alwin, I'm not serious about mixing OTC drugs with alcohol.</p>","fields":{"title":"I Got The Bug","date":"15 Jan 2003","url":"https://papascott.de/2003-01-15-i-got-the-bug/"}}},"pathContext":{"slug":"/2003-01-15-i-got-the-bug/","prev":{"fields":{"title":"To Book or Not To Book","date":"13 Jan 2003","slug":"/2003-01-13-to-book-or-not-to-book/"}},"next":{"fields":{"title":"Sounds of Silence","date":"17 Jan 2003","slug":"/2003-01-17-sounds-of-silence/"}}}}

